  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 36px;
  margin: 0;
  display: inline-block;
}

.wheather__celsius {
  opacity: 0.5;
}

.wheather__city {
  color: #FFF;
  font-family: Verdana;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%; /* 20px */
  margin: 0;
  padding: 0;
}

