  font-size: 3rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.currency {
  width: 250px;
  height: 120px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 40px;
  align-self: start;
  justify-self: end;
}

.currency__usd {
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
  font-family: Verdana;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%; /* 20px */
}

.currency__eur {
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
  font-family: Verdana;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%; /* 20px */
}

.links {
  width: 250px;
  height: 120px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 40px;
  align-self: end;
  justify-self: start;
}

.links__list {
  list-style: none;
  display: flex;
  gap: 12px;
  margin: 0;
  padding: 0;
}

.quots {
  width: 500px;
  height: 100px;
  display: flex;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 40px;
  align-self: end;
}

.quots__text {
  color: white;
  text-align: center;
  font-family: Verdana;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
  /* 20px */
  margin: 0;
  padding: 0;
}

.timer {
  width: 250px;
  height: 400px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 40px;
  align-self: end;
  justify-self: end;
}

.timer__mode {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  height: 50px;
  border-radius: 10px;
  border: 3px solid rgba(255, 255, 255, 0.5);
}

.timer__work,
.timer__rest {
  color: rgba(255, 255, 255, 0.5);
  font-family: Verdana;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%;
  /* 20px */
  margin: 0;
  padding: 0;
  margin: 0 auto;
}

.timer__line {
  position: absolute;
  top: -14px;
  left: 107px;
  width: 3px;
  height: 48px;
  background: rgba(255, 255, 255, 0.5);
}

.timer__work_active {
  color: white;
  font-weight: 700;
}

.timer__rest_active {
  color: white;
  font-weight: 700;
}

.timer__indication {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 5px solid #FF8A00;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.timer__time {
  color: #FFF;
  font-family: Verdana;
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%;
  /* 64px */
  margin: 0;
  padding: 0;
}

.timer__control {
  width: 200px;
  height: 50px;
}

.timer__list {
  list-style: none;
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
}

.timer__item {
  width: 24px;
  height: 24px;
}

.timer__btn {
  background-color: rgba(255, 255, 255, 0);
  color: black;
  border: none;
  cursor: pointer;
  width: 24px;
  height: 24px;
  margin: 0;
  padding: 0;
  opacity: 0.3;
}

.timer__btn:hover {
  opacity: 0.5;
  transition: 0.3s;
}

.timer__btn:active {
  opacity: 1;
  transition: 0.2s;
}

